<template>
  <div class="header">
  <!--<img src="../assets/logo.png" alt="">-->
    <h1 class="font-type"><a v-link="{path:'/'}">{{ msg | uppercase }}</a> <span>{{description}}</span></h1>
    <div class="menu-icon" @click="show = true"><span></span><span></span><span></span></div>
  </div>

  <div class="menu-content" v-show="show" transition="expand">
    <div class="close-icon" @click="show = false"><span></span><span></span></div>
    <h2>Vue Effect</h2>
    <ul id="menu-list">
      <!--<li v-for="list in lists" v-show="show" transition="runslide">{{ list.msg }}</li>-->
      <li><a @click="show = false" v-link="{path:'/'}">Home</a></li>
      <li><a @click="show = false" v-link="{path:'/write-poem'}">Part I - to be a Poet</a></li>
      <li><a @click="show = false" v-link="{path:'/poet'}">Part II - learn from Poets</a></li>
      <li><a @click="show = false" v-link="{path:'/shop'}">Part III - buy poems online</a></li>
    </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: 'Play Poem',
      description : '/  use Vue js',
      show:false,
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h1 {
  padding-top:10px;
  color: #fff;
  text-align: left;
  font-size: 24px;
  a{
    color: #fff;
    &:hover{
      color: #eee;
    }
  }
  span{
    padding-left: 10px;
    font-size: 12px;
    font-family: Arial;
    font-weight: lighter;
    color: #eee;
  }
}
.header{
  margin: 2% 3% 0%;
  width: 90%;
  height: 11%;
  padding:1% 2%;
  padding-top:2%;
  position: relative;
  animation: slide-in 1s 1;
  background: rgb(103, 209, 213);
}

@keyframes slide-in {
  0% {
    height: 0;
    padding-top:0;
    padding-bottom:0;
  }
  100% {
    height: 11%;
    padding-top:2%;
    padding-bottom:1%;
  }
}


.expand-transition {
  opacity: 0.95;
  height: 100%;
}

.expand-enter, .expand-leave {
  opacity: 0;
  height: 0;
}

.menu-icon{
  width: 30px;
  height: 30px;
  display: block;
  position: absolute;
  right: 2%;
  top: 45%;
  cursor: pointer;

  &:hover{
    span{
      background: #eee;
      width: 35px;
      margin-top: 7px;
      margin-bottom: 7px;
      &:nth-child(2){
        transform: rotateY(40deg);
      }
      &:nth-child(3){
        transform: rotateY(70deg);
      }
    }
  }

  span{
    margin-top: 5px;
    margin-bottom: 5px;
    width: 30px;
    height: 2px;
    display: block;
    background: #fff;
    transition: all .5s ease;

  }

}
  .close-icon{
    @extend .menu-icon;
    top:5%;

    &:hover{
      span{
        width: 50px;
        background:#333;
        &:first-child{
          transform: rotateZ(-155deg);
        }
        &:last-child{
          transform: rotateZ(155deg);
        }
      }
    }
    span{
      background: #333;
      width: 50px;
      height: 2px;
      display: block;
      position: absolute;
      top:0;
      right: 0;
      &:first-child{
        transform: rotateZ(-135deg);
      }
      &:last-child{
        transform: rotateZ(135deg);
      }
    }
  }
.menu-content{
  width: 100%;
  display: block;
  position: fixed;
  top:0;
  left:0;
  z-index: 999;
  background: #ccc;
  transition: all .5s ease-in;
  h2{
      font-size: 30px;
    }
  ul{
    display:list-item;
    font-family: "Adobe Gothic Std";
    text-align: center;
    
    li{
      margin-bottom: 10px;
      list-style: none;
      font-family: Arial;
      font-weight: lighter;
      font-size: 18px;
      a{
        position:relative;
        color: #fff;
        &:hover{
          color: #42b983;
        }
      }
    }
  }
}
</style>



