<template>
  <div class="content" v-bind:class="bgClass">
    <div class="bg"></div>
    <p class="title"><span>Today is {{today}}.</span>{{sayMsg}}</p>
    <p class="colorNav"><span @click="bgClass = 'bg-red',showPoet ='poet-one'"></span>
      <span @click="bgClass = 'bg-blue',showPoet ='poet-two'"></span>
      <span @click="bgClass = 'bg-purple',showPoet ='poet-three'"></span>
    </p>
    <div class="sub-content">
        <div class="fix-content">
          <div v-if="showPoet =='poet-one'" transition="fade">
            <h2 class="font-type">{{contentTitle | uppercase}}</h2>
            <p>Up in the Northern Mountains among white clouds,<br>
              Being a recluse I easily in my environs took delight.<br>
              Imagining that I might see you I started to climb,<br>
              No sight of you saw my hope dashing away with the wild geese that flew by.<br>
              <br>
              Melancholy rose as the veil of twilight fell, <br>
              Early autumn seemed the time when sentiments run spry. <br>
              Every now and then I could see returning villagers,<br>
              On the sands they trod and by the dock they waited for ferry rides.<br>
              <br>
              In the distance trees towered into the sky in the shape of shepherd's purse, <br>
              By the river the sand bar appeared small and moon-like.<br>
              I wondered if we could have a vat of wine carted up here?<br>
              And we shall on the Double Ninth drink to the good old times.

              </p>
          </div>
          <div v-if="showPoet =='poet-two'" transition="fade">
            <h2 class="font-type">{{contentTitle2 | uppercase}}</h2>
            <p>I have long been restrained by the attire of office, <br>
              An exile to a remote southern state comes as a blessing in disguise. <br>
              I'd spend my spare time with neighbouring farmers and growers, <br>
              Into the mountains and woods I'd wander sometimes. <br>
              At dawn I till the land and tend to dewy weeds, <br>
              I pole my own boat striking stones in the creek at night, <br>
              Where I live is out of people's way, <br>
              Allowing me to sing on and on to the blue southern skies.
              </p>
              <p>-</p>
              <h2>柳宗元 – 溪居</h2>
              <p>久為簪組束， <br>
                幸此南夷謫。<br>
                閒依農圃鄰， <br>
                偶似山林客。<br>
                曉耕翻露草， <br>
                夜榜響谿石，<br>
                來往不逢人， <br>
                長歌楚天碧。</p>
          </div>
          <div v-if="showPoet =='poet-three'" transition="fade">
            <h2 class="font-type">{{contentTitle3 | uppercase}}</h2>
            <p>Seeing off the dead mourners lose their voice, <br>
              Seeing off the living is often just as saddening. <br>
              From south of the Yangtze, the malaria ridden land, <br>
              Came no news of my exiled friend.<br>
              <br>
              At night I dream of this old friend of mine, <br>
              It must be a reflection of my thoughts of him in daytime. <br>
              But he must be deep in toils, <br>
              How could he have wings to hither fly? <br>
              What I saw may not be his living self, <br>
              The distance is far too great and anything could arise.<br>
            <br>
              In my dream he arrived amid maple green, <br>
              When he left, deep in blackness was the dividing range. <br>
              Moonlight shone all over roof raking and beams, <br>
              I wondered whether it could return colour to his face. <br>
              The water is deep and wild billows make, <br>
              I'd like to cautioned him to flood dragons guard against.</p>
              <p>-</p>
              <h2>杜甫 – 夢李白二首其一</h2>
              <p>
                死別已吞聲， 
                生別常惻惻。<br>
                江南瘴癘地， 
                逐客無消息。<br>
<br>
                故人入我夢， 
                明我長相憶。<br>
                君今在羅網， 
                何以有羽翼？<br>
                恐非平生魂， 
                路遠不可測。<br>
<br>
                魂來楓葉青， 
                魂返關山黑；<br>
                落月滿屋梁， 
                猶疑照顏色。<br>
                水深波浪闊， 
                無使蛟龍得。<br>
              </p>
              </p>
          </div>
          
        </div>
    </div>
  </div>
</template>

<script>
  var dayArry = ['Monday','Tuesday','Wednesday','Thursday','Friday','Saturday'],
      today = dayArry[new Date().getDay() - 1];

export default {
  data () {
    return {
      sayMsg: 'Have a wonderful day!',
      today : today,
      bgClass: 'bg-red',
      showPoet:'poet-one',
      contentTitle : 'MENG Haoran – To Zhang on a Climb up Mount Orchid in Autumn',
      contentTitle2 : 'LIU Zongyuan – Life by a Creek',
      contentTitle3 : 'DU Fu – Dreaming of Li Bai I of II'
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.sub-content{
  width: 50%;
  height: 90%;
  display: block;
  overflow-y:scroll; 
  position:absolute;
  right: 2%;
  top: 5%;
  background-color:#fff;
  box-shadow:0px 3px 10px #555555;
}

.content{
  .bg{
    width: 100%;
    height: 100%;
    display: block;
    opacity: .3;
    position:absolute;
    top: 0;
    left: 0;
    background-size:cover;
    background-repeat:no-repeat;
  }
  .title{
    text-align: left;
    font-size: 48px;
    line-height: 48px;
    color: #fff;

    span{
      font-size: 18px;
      display: block;
    }
  }
}
.fix-content{
  padding:30px;
  h2{
    line-height: 40px;
  }
  p{
    line-height: 30px;
  }
}
.colorNav{
  position:absolute;
  top:90%;
  text-align: center;
  span{
    width: 15px;
    height:15px;
    margin-right: 15px;
    display: inline-block;
    border-radius: 15px;
    background-color:#fff;
    cursor: pointer;
    &:first-child:hover{
      background-color:rgb(255, 186, 116);
    }
    &:nth-child(2):hover{
      background-color: rgb(0, 127, 255);
    }
    &:nth-child(3):hover{
      background-color: rgb(91, 91, 174);
    }
  }
}

@keyframes red-in {
  0% {
    background-color: rgb(255, 186, 116);
  }
  50% {
    background-color:rgb(236, 70, 112);
  }
  100% {
    background-color: rgb(255, 186, 116);
  }
}
@keyframes zoom-out {
  0% {
    transform: scale(1,1);
  }
  100% {
    transform: scale(1.5,1.5);
  }
}
@keyframes blue-in {
  0% {
    background-color: rgb(0, 127, 255);
  }
  50% {
    background-color:rgb(0, 212, 255);
  }
  100% {
    background-color: rgb(0, 127, 255);
  }
}
@keyframes purple-in {
  0% {
    background-color: rgb(91, 91, 174);
  }
  50% {
    background-color:rgb(158, 91, 174);
  }
  100% {
    background-color: rgb(91, 91, 174);
  }
}
.bg-red{
  animation:slide-run 1s 1,red-in 5s infinite;
  .bg{
    animation:zoom-out 70s infinite;
    background-image:url('../assets/red.jpg');
  }
}
.bg-blue{
  animation:slide-run 1s 1,blue-in 5s infinite;
  .bg{
    animation:zoom-out 70s infinite;
    background-image:url('../assets/blue.jpg');
  }
