<template>
  <div class="content">

  	<div class="pro-list">
  		<h2 class="font-type">Check out your Cart</h2>
  		<table class="cart-color">
			<thead class="font-type">
				<tr>
					<th width="60%">Name</th>
					<th width="15%">Price</th>
					<th width="20%">Quantity</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="item in buylist">
					<td>{{item.name}}</td>
					<td>{{item.price}}</td>
					<td>{{item.num}}</td>
				</tr>
			</tbody>
		</table>
		<div class="choose">
			<button class="btn-cancel" v-link="{path:'/shop'}">Cancel</button>
			<button class="btn-shop" @click="showLogin">Next Step</button>
		</div>
		<div class="login" v-show="isLogin" transition="fade">
			<h3 class="font-type">Login ?</h3>
		 	<validator name="validation1">
			    <form novalidate @submit.prevent="loginIn">
					<table class="cart-color txt-left">
						<tbody>
							<tr>
								<td width="10%">Phone Number</td>
								<td width="35%"><input type="text" v-validate:username="nameRules">
									<p class="red" v-if="$validation1.username.required && isSent">{{ $validation1.username.required }}</p>
									<p class="red" v-if="$validation1.username.minlength && $validation1.username.modified">{{ $validation1.username.minlength }}</p>
								</td>
								<td width="10%">password</td>
								<td width="35%"><input type="password" name="password" v-validate:password="passRules">
								<p class="red" v-if="$validation1.password.required && isSent">{{ $validation1.password.required }}</p>
								<p class="red" v-if="$validation1.password.minlength && $validation1.password.modified">{{ $validation1.password.minlength }}</p>
								</td>
								<td width="10%"><button class="btn-shop">Send</button></td>
								
							</tr>

						</tbody>
					</table>

				</form>
		  	</validator>
		</div>
		<div class="member-infor" v-show="userInfo" transition="fade">
			<h3 class="font-type">Member information</h3>
			<table class="cart-color">
				<thead class="font-type">
					<tr>
						<th width="20%">Name</th>
						<th width="15%">Birth</th>
						<th width="15%">Phone Number</th>
						<th width="50%">Address</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td>{{memberInfo.name}}</td>
						<td>{{memberInfo.birth}}</td>
						<td>{{memberInfo.number}}</td>
						<td>{{memberInfo.address}}</td>
					</tr>
				</tbody>
			</table>
			<div class="choose">
				<button class="btn-cancel" v-link="{path:'/shop'}">Cancel</button>
				<button class="btn-shop" @click="orderDone">Next Step</button>
			</div>
			<order v-show="orderMsg" transition="fade"></order>
		</div>

  	</div>


  </div>
</template>

<script>
import apiList from './apiList'
import cart from './cart'

var tempStorage = window.sessionStorage;
export default {
  data () {
    return {
    	buylist:[],
    	memberInfo:[],
    	isLogin:false,
    	isMember:null,
    	isSent:false,
    	userInfo:false,
    	orderMsg:false,
    	nameRules:{ required: {rule:true,message:'Required your phone Number!'}, 
    				minlength: {rule:10,message:'your phone Number short too!'} },
    	passRules:{ required: {rule:true,message:'Required your password!'}, 
				minlength: {rule:8,message:'your password short too!'} }
    }
  },

  components:{
  	'order':{
  		template:'<div class="success-order"><h3>Your order is success!</h3></div>',
  	}
  },
  ready:function(){
  	this.getBuylist();
  },
  methods:{
  	showLogin:function(event){
  		this.isLogin = true;
  		event.currentTarget.parentNode.style.display = "none";
  	},
  	getBuylist:function () {
		dataList = JSON.parse(window.sessionStorage.getItem('cartList'));
		this.$set('buylist',dataList);
  	},
  	loginIn:function(){
  		console.log('you login in now!');
  		this.isSent = true;
  		if(this.$validation1.username.valid && this.$validation1.password.valid){
  			this.getMemberInfo();
  		}
  	},
  	getMemberInfo:function(){
  		console.log('check member info!');
  		this.$http.get('./static/member.json')
	      .then((resp) => {
	      	console.log(resp.data[0]);
	      	this.$set('memberInfo',resp.data[0]);
	      });
	    this.isLogin = false;
	    this.userInfo = true;
  	},
  	orderDone:function(){
  		console.log('your order is done!');
  		this.orderMsg = true;
  	}

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h2{
	font-size:30px;
}
.content{
	padding:0;
	width:94%;
	height: 72%;
}
.pro-list{
	width:100%;
	height: 100%;
	padding-bottom:30px;
	display:block;
	overflow-y:scroll;
	.choose{
		margin-top: 20px;
	}
}
.btn-cancel{
	float:left;
}
.btn-shop{
	float:right;
}
.member-infor{
	margin-bottom:20px;
	transition: all .7s ease;
}
.book-list{
	li{
		width: 220px;
		height: 280px;
		padding:20px; 
		margin:20px 10px;
		display: inline-block;
		list-style: none;
		transition: all .5s ease;
		div{
			width: 220px;
			height: 220px;
			border-radius: 100%;
			overflow: hidden;
			position:relative;
			cursor: pointer;
			text-align: center;
			transition: all .5s ease;
			&:before{
				content: url('../assets/zoom.png');
				width: 100%;
				height: 55%;
				padding-top:45%;
				display: none;
				position: absolute;
				top:0;
				left: 0;
				z-index: 9;
				color: #fff;
				vertical-align: middle;
				background:rgba(0,0,0,0.7);
			}
			img{
				width: 100%;
				height: auto;
			}
			&:hover{
				&:before{
					display: block;
				}
			}
		}
		h4{
			margin-bottom: 0;
		}
		p{
			margin:10px 0;
		}
		&:hover{
			background-color: #fff;
			
		}
	}
}
.choose{
	width:90%;
	margin:0 auto;
	overflow:hidden;
}
.login{
	margin-top:30px;
	clear:both;
	h3{
	}
}
.success-order{
	width:100%;
	height: 100%;
	display:block;
	position:absolute;
	top:0;
	left:0;
	z-index:999;
	background:#eee;
}
.cart-color{
	margin:0 auto;
	width:90%;
	font-size:14px;
	border-collapse: collapse;
	thead{
		tr{
			border-top:1px solid #fff;
			border-bottom:1px solid #fff;
			th{
				padding:10px 0;
			}
		}

	}
	tr{
		border-bottom:1px solid #fff;
		td{
			padding:10px 0;
			position: relative;
			font-weight: lighter;
			a{
				width: 20px;
				height: 17px;
				padding-top:3px;
				display: block;
				color: #000;
				text-align: center;
				border-radius:50px;
				background:rgba(255,255,255,0.8);
				cursor: pointer;
				&:hover{
					background:#67d1d5;
				}
			}
			input{
				width: 90%;
				background:#fff;
				border:none;
				&:focus{
					background:#ccc;
				}
			}
			p{
				position:absolute;
				left: 0;
				bottom:-40px;
			}
		}
	}
}


</style>



