thern state comes as a blessing in disguise. <br _v-af8f28fa=\"\"> I'd spend my spare time with neighbouring farmers and growers, <br _v-af8f28fa=\"\"> Into the mountains and woods I'd wander sometimes. <br _v-af8f28fa=\"\"> At dawn I till the land and tend to dewy weeds, <br _v-af8f28fa=\"\"> I pole my own boat striking stones in the creek at night, <br _v-af8f28fa=\"\"> Where I live is out of people's way, <br _v-af8f28fa=\"\"> Allowing me to sing on and on to the blue southern skies. </p> <p _v-af8f28fa=\"\">-</p> <h2 _v-af8f28fa=\"\">柳宗元 – 溪居</h2> <p _v-af8f28fa=\"\">久為簪組束， <br _v-af8f28fa=\"\"> 幸此南夷謫。<br _v-af8f28fa=\"\"> 閒依農圃鄰， <br _v-af8f28fa=\"\"> 偶似山林客。<br _v-af8f28fa=\"\"> 曉耕翻露草， <br _v-af8f28fa=\"\"> 夜榜響谿石，<br _v-af8f28fa=\"\"> 來往不逢人， <br _v-af8f28fa=\"\"> 長歌楚天碧。</p> </div> <div v-if=\"showPoet =='poet-three'\" transition=fade _v-af8f28fa=\"\"> <h2 class=font-type _v-af8f28fa=\"\">{{contentTitle3 | uppercase}}</h2> <p _v-af8f28fa=\"\">Seeing off the dead mourners lose their voice, <br _v-af8f28fa=\"\"> Seeing off the living is often just as saddening. <br _v-af8f28fa=\"\"> From south of the Yangtze, the malaria ridden land, <br _v-af8f28fa=\"\"> Came no news of my exiled friend.<br _v-af8f28fa=\"\"> <br _v-af8f28fa=\"\"> At night I dream of this old friend of mine, <br _v-af8f28fa=\"\"> It must be a reflection of my thoughts of him in daytime. <br _v-af8f28fa=\"\"> But he must be deep in toils, <br _v-af8f28fa=\"\"> How could he have wings to hither fly? <br _v-af8f28fa=\"\"> What I saw may not be his living self, <br _v-af8f28fa=\"\"> The distance is far too great and anything could arise.<br _v-af8f28fa=\"\"> <br _v-af8f28fa=\"\"> In my dream he arrived amid maple green, <br _v-af8f28fa=\"\"> When he left, deep in blackness was the dividing range. <br _v-af8f28fa=\"\"> Moonlight shone all over roof raking and beams, <br _v-af8f28fa=\"\"> I wondered whether it could return colour to his face. <br _v-af8f28fa=\"\"> The water is deep and wild billows make, <br _v-af8f28fa=\"\"> I'd like to cautioned him to flood dragons guard against.</p> <p _v-af8f28fa=\"\">-</p> <h2 _v-af8f28fa=\"\">杜甫 – 夢李白二首其一</h2> <p _v-af8f28fa=\"\"> 死別已吞聲， 生別常惻惻。<br _v-af8f28fa=\"\"> 江南瘴癘地， 逐客無消息。<br _v-af8f28fa=\"\"> <br _v-af8f28fa=\"\"> 故人入我夢， 明我長相憶。<br _v-af8f28fa=\"\"> 君今在羅網， 何以有羽翼？<br _v-af8f28fa=\"\"> 恐非平生魂， 路遠不可測。<br _v-af8f28fa=\"\"> <br _v-af8f28fa=\"\"> 魂來楓葉青， 魂返關山黑；<br _v-af8f28fa=\"\"> 落月滿屋梁， 猶疑照顏色。<br _v-af8f28fa=\"\"> 水深波浪闊， 無使蛟龍得。<br _v-af8f28fa=\"\"> </p> <p _v-af8f28fa=\"\"></p> </div> </div> </div> </div> ";


