<template>
  <div class="content">

  	<div class="pro-list">
  		<h2 class="font-type">Poems Online Shop</h2>
  		<!--shopping cart-->
		<cart></cart>
  		<ul class="book-list">
  			<li v-for="poem in booklist">
  				<div class="cover" @click="look" num="{{poem.no}}">
  					<img src="../assets/getImage.jpg" alt="">
  				</div>
				<h4>{{poem.book}}</h4>	
				<p>NT$ {{poem.price}}</p>
  			</li>
  		</ul>
	    <api-list using="plugin: vue-resource , vue-router / vue api : v-on:click(@click) , v-for , v-show , v-modal , transition , props , components , methods , ready , $set"></api-list>
  	</div>

	<!-- book intro -->
    <div class="book-layer" v-if="isOpened" transition="fade">
      	
    	<div class="book-detail">
	    	<div class="close" @click="isOpened = false,imagesOpend = true"><span></span><span></span></div>
	    	
		  	<h4>{{thebook.title}}</h4>
		  	<ul class="font-type">
		  		<li @click="imagesOpend = true" v-bind:class="[ imagesOpend ? 'active':'']">Images</li>
		  		<li @click="imagesOpend = false" v-bind:class="[ imagesOpend ? '':'active']">Details</li>
		  	</ul>
			<div class="tool">
				<button class="btn-dec" @click="decrease">-</button><input type="text" value="{{buyNum}}"><button class="btn-add" @click="add">+</button>
				<p><button class="btn-shop" @click="addToCart">Add to Cart</button></p>
			</div>
			<div class="msg-ok" v-show="isadded">
	    		<h4>Adding is successful.</h4>
	    	</div>
		  	<!-- book image -->
		  	<div v-if="imagesOpend" class="product-frame">
		  		
		  		<figure>
		  			<img :src="thebook.cover" v-if="isSelected == 'cover'" >
		  			<img :src="thebook.picurl" v-if="isSelected == 'pic'" >
		  			<img :src="thebook.picurl2" v-if="isSelected == 'pic2'" >
		  		</figure>
		  		<ul class="pic-list">
		  			<li @click="isSelected ='cover' "><img :src="thebook.cover"></li>
		  			<li @click="isSelected ='pic' "><img :src="thebook.picurl"></li>
		  			<li @click="isSelected ='pic2' "><img :src="thebook.picurl2"></li>
		  		</ul>
		  	</div>
		  	<!-- book infor -->
		  	<div v-else class="product-frame ">
		  		<p>Price / NT $ {{thebook.price}}</p>
		  		<p>Author / {{thebook.author}}</p>
		  		<p>Paperback / {{thebook.pages}} pages</p>
		  	</div>
    	</div>
	</div>

  </div>
</template>

<script>
import apiList from './apiList'
import cart from './cart'

var tempStorage = window.sessionStorage;
export default {
  data () {
    return {
    	booklist:[],
    	thebook:[],
    	//tempList :[],
    	buyNum:1,
    	allBuyNum:0,
    	isOpened:false,
    	isadded:false,
    	imagesOpend:true,
    	isSelected:'cover',
    }
  },
  components:{
    apiList,
    cart
  },
  ready:function(){
  	this.getBooklist();
  },
  methods:{
  	getBooklist:function () {
      this.$http.get('./static/products.json')
      .then((response) => {
      	//console.log(response);
      	this.$set('booklist',response.data);
      }) 
  	},
  	look:function(event){
  		this.cartopened = false;
  		this.isOpened = true;
  		this.isadded = false;
  		this.buyNum = 1;
  		var bookNum = event.currentTarget.getAttribute("num");
  		//console.log(bookNum);
  		this.thebook = {
  			 title   : this.booklist[bookNum].book,
  			 price   : this.booklist[bookNum].price,
  			 author  : this.booklist[bookNum].author,
  			 pages   : this.booklist[bookNum].pages,
  			 cover   : this.booklist[bookNum].cover,
  			 picurl  : this.booklist[bookNum].picurl,
  			 picurl2 : this.booklist[bookNum].picurl2,
  		}
  	},
  	decrease:function(){
  		if(this.buyNum !== 1){
	  		this.$set('buyNum', this.buyNum - 1);
  		}
  	},
  	add:function(){
  		if(this.buyNum == 10){
  			alert('書籍購買上限為10本！');
  		}else{
	  		this.$set('buyNum', this.buyNum+1);
  		}
  	},
  	addToCart:function(){
  		console.log('add success!');
  		var selectedBook = {
  			name : this.thebook.title,
  			price : this.thebook.price,
  			num : this.buyNum
  		}
  		tempList.push(selectedBook);
  		tempStorage.setItem('cartList',JSON.stringify(tempList));
  		//dataList = JSON.parse(window.sessionStorage.getItem('cartList'));
  		console.log(tempStorage.getItem('cartList'));
  		this.isadded = true;

  	}

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h2{
	font-size:30px;
}
.content{
	padding:0;
	width:94%;
	height: 72%;
}
.pro-list{
	width:100%;
	height: 100%;
	padding-bottom:30px;
	display:block;
	overflow-y:scroll;
}
.book-list{
	li{
		width: 220px;
		height: 280px;
		padding:20px; 
		margin:20px 10px;
		display: inline-block;
		list-style: none;
		transition: all .5s ease;
		div{
			width: 220px;
			height: 220px;
			border-radius: 100%;
			overflow: hidden;
			position:relative;
			cursor: pointer;
			text-align: center;
			transition: all .5s ease;
			&:before{
				content: url('../assets/zoom.png');
				width: 100%;
				height: 55%;
				padding-top:45%;
				display: none;
				position: absolute;
				top:0;
				left: 0;
				z-index: 9;
				color: #fff;
				vertical-align: middle;
				background:rgba(0,0,0,0.7);
			}
			img{
				width: 100%;
				height: auto;
			}
			&:hover{
				&:before{
					display: block;
				}
			}
		}
		h4{
			margin-bottom: 0;
		}
		p{
			margin:10px 0;
		}
		&:hover{
			background-color: #fff;
			
		}

	}
}
.book-layer{
	width:100%;
	height: 100%;
	display:block;
	position:absolute;
	top:0;
	left:0;
	z-index:90;
	background:rgba(0,0,0,0.7);
	.book-detail{
		width: 700px;
		height: 70%;
		display: block;
		position:absolute;
		top:50%;
		left:50%;
		transform: translate(-50%,-50%);
		background-color:#fff;
		.msg-ok{
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			top:0;
			left: 0;
			z-index: 88;
			background:#fff;
		}
		> ul{
			padding-left:0;
			margin-top: 10px;
			width: 100px;
			position:absolute;
			left: 0;
			top:45px;
			li{
				margin-bottom: 5px;
				padding:5px 10px;
				border-bottom:1px solid #555;
				list-style: none;
				cursor: pointer;
				&:hover{
					color: #67d1d5;
				}
				&.active{
					color: #67d1d5;
					border-color: #67d1d5;
				}
			}
		}
		.tool{
			width: 100px;
			position:absolute;
			left: 0;
			bottom:15px;
			input[type="text"]{
				width: 30px;
				text-align: center;
			}
		}
	}
}
 .product-frame{
 	width: 79%;
 	height: 78%;
 	padding:0% 3%;
 	margin-left:100px;
 	position:relative;
 	text-align: left;
	//background-color:#eee;
	.pic-list{
		margin-top: 0;
		position:absolute;
		right: 10px;
		top: 0px;
		list-style: none;
		width: 50px;
		li{
			width: 100%;
			img{
				width: 100%;
			}
		}
	}
	figure{
		width: 500px;
		height: 100%;
		margin-left: 0;
		text-align: center;
		border:1px solid #eee;
		img{
			height: 100%;
		}
	}
 }


</style>



