<template>
  <div class="content">
    <span class="title">Write a poem</span>
    <div class="add" @click="open = true"><span></span><span></span></div>

    <ul class="poem-list">
      <li v-for="poem in poemlist">
        <h4>{{ poem.title }}</h4>
        <p>{{ poem.poem }}</p>
        <span class="line"></span>
        <p class="gray">Author / {{ poem.name }}</p>
        <p class="gray">Type / {{ poem.type }}</p>
      </li>
    </ul>
    <api-list using="plugin: vue-resource , vue-router / vue api : v-on:click(@click) , v-for , v-show , v-modal , transition , props , components , methods , ready , $set"></api-list>

  </div>
  <!--commet modal-->
  <div class="box" v-show="open" transition="fade" >
        <div class="layer" @click="open = false"></div>
        <div class="modal">
          <div class="error" v-show="errorMsg">
            <div class="msg">
              <p>All the field must be filled.</p>
              <button class="confirm btn-error" @click="errorMsg = false">close</button>
            </div>
          </div>
          <div class="close" @click="open = false"><span></span><span></span></div>
          <span class="title">Write Your Poem</span>
          <p>
          <span>Name / </span><input type="text" v-model="createPoem.name">
          </p>
          <p>
          <span>Title / </span><input type="text" v-model="createPoem.title">
          </p>
          <p>Poem / <br>
          <textarea debounce="300" v-model="createPoem.poem"></textarea></p>
            <p><span>Type / </span><input type="radio" id="love" value="love" v-model="createPoem.type">
            <label for="love">Love</label>
            <input type="radio" id="life" value="life" v-model="createPoem.type">
            <label for="life">Life</label>
            <input type="radio" id="encourage" value="encourage" v-model="createPoem.type">
            <label for="encourage">Encourage</label>
            <input type="radio" id="history" value="history" v-model="createPoem.type">
            <label for="history">History</label>
          </p>
          <button type="submit" class="confirm" @click="sendPoem(),open = false">submit</button>
        </div>
  </div>

</template>

<script>
import apiList from './apiList'

export default {
  components:{
    apiList,
  },
  data () {
    return {
      thingOne:true,
      open:false,
      showloading:false,
      errorMsg:false,
      showPoem:true,
      classExpand:false,
      openExpand:'expand',
      poemlist:[],
      createPoem:{}
    }
  },
  ready:function(){   
      var vm = this;
      vm.$http.get('./static/poem.json')
          .then((response) => {
              console.log(Object.keys(response.data).length);
              vm.$set('poemlist',response.data);
        });   
  },
  methods:{
    sendPoem:function(){
        console.log(this.createPoem);
        this.poemlist.push(this.createPoem);
        this.clearPoem();
    },
    clearPoem:function(){
      this.createPoem = {};
    }
  }

}

