    padding-left:30px;
    display: block;
    margin-top: 15px;
    position:relative;
    font-size: 24px;
    line-height: 60px;
    color: #000;

  }
.poem-list{
  padding-left: 6px;
  margin:0;
  text-align: center;

  li{
    list-style: none;
    padding:0 1%;
    padding-top:15px;
    padding-bottom:15px;
    margin:5px;
    width: 17%;
    height: 200px;
    overflow: hidden;
    display: inline-block;
    background-color:#fff;
    transition: all 0.5s ease;
    &.expand{
        width: 98%;
        height: auto;
    }    
  &:hover{
      box-shadow:0px 0px 10px #666;
    }
    h4{
      line-height: 20px;
      margin:5px 0;
    }
    p{
      line-height: 24px;
      margin:0px;
    }
