<template>
<button class="btn-use" @click="openUsing ? openUsing = false : openUsing = true">see using api</button>
  <div class="api" v-if="openUsing" transition="expand">
     <span class="green">{{using}}</span>
  </div>
</template>

<script>
export default {
  data () {
    return {
    	openUsing:false,
    }
  },
  props: ['using'],
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.expand-transition {
  transition: all 1s ease;
  width: 87%;
}

/* .expand-enter 定义进入的开始状态 */
/* .expand-leave 定义离开的结束状态 */
.expand-enter, .expand-leave {
  //opacity: 0;
  width: 0;
}
.api{
	margin:0 15px;
	padding:5px 10px;
	height: auto;
	display:block;
	float:left;
	//clear:both;
	line-height:14px;
	border-radius:5px;
	vertical-align:middle;
	background-color:#fff;
	//border-top:1px solid #67d1d5;
}
.green{
  color: #67d1d5;
  font-size: 13px;
}
.btn-use{
	width: 100px;
	padding:5px 10px;
	position:relative;
	float:left;
	border:none;
	background-color:#67d1d5;
	color:#fff;
	border-radius:5px;
	cursor:pointer;
	&:hover:after{
		display: block;
	}
	&:after{
		content: '';
		width: 0;
		height: 0;
		display: none;
		border-style: solid;
		border-width: 5px 0 5px 5px;
		border-color: transparent transparent transparent #67d1d5;

		position:absolute;
		top:8px;
		right:-5px;
	}
}

</style>


