	module.exports = " <div class=content v-bind:class=bgClass _v-af8f28fa=\"\"> <div class=bg _v-af8f28fa=\"\"></div> <p class=title _v-af8f28fa=\"\"><span _v-af8f28fa=\"\">Today is {{today}}.</span>{{sayMsg}}</p> <p class=colorNav _v-af8f28fa=\"\"><span @click=\"bgClass = 'bg-red',showPoet ='poet-one'\" _v-af8f28fa=\"\"></span> <span @click=\"bgClass = 'bg-blue',showPoet ='poet-two'\" _v-af8f28fa=\"\"></span> <span @click=\"bgClass = 'bg-purple',showPoet ='poet-three'\" _v-af8f28fa=\"\"></span> </p> <div class=sub-content _v-af8f28fa=\"\"> <div class=fix-content _v-af8f28fa=\"\"> <div v-if=\"showPoet =='poet-one'\" transition=fade _v-af8f28fa=\"\"> <h2 class=font-type _v-af8f28fa=\"\">{{contentTitle | uppercase}}</h2> <p _v-af8f28fa=\"\">Up in the Northern Mountains among white clouds,<br _v-af8f28fa=\"\"> Being a recluse I easily in my environs took delight.<br _v-af8f28fa=\"\"> Imagining that I might see you I started to climb,<br _v-af8f28fa=\"\"> No sight of you saw my hope dashing away with the wild geese that flew by.<br _v-af8f28fa=\"\"> <br _v-af8f28fa=\"\"> Melancholy rose as the veil of twilight fell, <br _v-af8f28fa=\"\"> Early autumn seemed the time when sentiments run spry. <br _v-af8f28fa=\"\"> Every now and then I could see returning villagers,<br _v-af8f28fa=\"\"> On the sands they trod and by the dock they waited for ferry rides.<br _v-af8f28fa=\"\"> <br _v-af8f28fa=\"\"> In the distance trees towered into the sky in the shape of shepherd's purse, <br _v-af8f28fa=\"\"> By the river the sand bar appeared small and moon-like.<br _v-af8f28fa=\"\"> I wondered if we could have a vat of wine carted up here?<br _v-af8f28fa=\"\"> And we shall on the Double Ninth drink to the good old times. </p> </div> <div v-if=\"showPoet =='poet-two'\" transition=fade _v-af8f28fa=\"\"> <h2 class=font-type _v-af8f28fa=\"\">{{contentTitle2 | uppercase}}</h2> <p _v-af8f28fa=\"\">I have long been restrained by the attire of office, <br _v-af8f28fa=\"\"> An exile to a remote southern state comes as a blessing in disguise. <br _v-af8f28fa=\"\"> I'd spend my spare time with neighbouring farmers and growers, <br _v-af8f28fa=\"\"> Into the mountains and woods I'd wander sometimes. <br _v-af8f28fa=\"\"> At dawn I till the land and tend to dewy weeds, <br _v-af8f28fa=\"\"> I pole my own boat striking stones in the creek at night, <br _v-af8f28fa=\"\"> Where I live is out of people's way, <br _v-af8f28fa=\"\"> Allowing me to sing on and on to the blue southern skies. </p> <p _v-af8f28fa=\"\">-</p> <h2 _v-af8f28fa=\"\">柳宗元 – 溪居</h2> <p _v-af8f28fa=\"\">久為簪組束， <br _v-af8f28fa=\"\"> 幸此南夷謫。<br _v-af8f28fa=\"\"> 閒依農圃鄰， <br _v-af8f28fa=\"\"> 偶似山林客。<br _v-af8f28fa=\"\"> 曉耕翻露草， <br _v-af8f28fa=\"\"> 夜榜響谿石，<br _v-af8f28fa=\"\"> 來往不逢人， <br _v-af8f28fa=\"\"> 長歌楚天碧。</p> </div> <div v-if=\"showPoet =='poet-three'\" transition=fade _v-af8f28fa=\"\"> <h2 class=font-type _v-af8f28fa=\"\">{{contentTitle3 | uppercase}}</h2> <p _v-af8f28fa=\"\">Seeing off the dead mourners lose their voice, <br _v-af8f28fa=\"\"> Seeing off the living is often just as saddening. <br _v-af8f28fa=\"\"> From south of the Yangtze, the malaria ridden land, <br _v-af8f28fa=\"\"> Came no news of my exiled friend.<br _v-af8f28fa=\"\"> <br _v-af8f28fa=\"\"> At night I dream of this old friend of mine, <br _v-af8f28fa=\"\"> It must be a reflection of my thoughts of him in daytime. <br _v-af8f28fa=\"\"> But he must be deep in toils, <br _v-af8f28fa=\"\"> How could he have wings to hither fly? <br _v-af8f28fa=\"\"> What I saw may not be his living self, <br _v-af8f28fa=\"\"> The distance is far too great and anything could arise.<br _v-af8f28fa=\"\"> <br _v-af8f28fa=\"\"> In my dream he arrived amid maple green, <br _v-af8f28fa=\"\"> When he left, deep in blackness was the dividing range. <br _v-af8f28fa=\"\"> Moonlight shone all over roof raking and beams, <br _v-af8f28fa=\"\"> I wondered whether it could return colour to his face. <br _v-af8f28fa=\"\"> The water is deep and wild billows make, <br _v-af8f28fa=\"\"> I'd like to cautioned him to flood dragons guard against.</p> <p _v-af8f28fa=\"\">-</p> <h2 _v-af8f28fa=\"\">杜甫 – 夢李白二首其一</h2> <p _v-af8f28fa=\"\"> 死別已吞聲， 生別常惻惻。<br _v-af8f28fa=\"\"> 江南瘴癘地， 逐客無消息。<br _v-af8f28fa=\"\"> <br _v-af8f28fa=\"\"> 故人入我夢， 明我長相憶。<br _v-af8f28fa=\"\"> 君今在羅網， 何以有羽翼？<br _v-af8f28fa=\"\"> 恐非平生魂， 路遠不可測。<br _v-af8f28fa=\"\"> <br _v-af8f28fa=\"\"> 魂來楓葉青， 魂返關山黑；<br _v-af8f28fa=\"\"> 落月滿屋梁， 猶疑照顏色。<br _v-af8f28fa=\"\"> 水深波浪闊， 無使蛟龍得。<br _v-af8f28fa=\"\"> </p> <p _v-af8f28fa=\"\"></p> </div> </div> </div> </div> ";

