<template>
  <div class="content">
      <div class="poet {{ poet.bkclass }}" v-for="poet in poetlist">
    	  <figure></figure>
	      <div class="poet-content font-type">
	      	<h4 class="title">{{ poet.name }}</h4>
	        <p><{{ poet.poem }}></p>
	        <span class="line"></span>
	        <p>{{ poet.year }}</p>
	        <button class="btn-more" @click="szymbo = true">more</button>
	      </div>

      </div>
      <div class="poet-detail" v-if="szymbo" transition="width">
      	<div class="close" @click="szymbo = false"><span></span><span></span></div>
      	<poet-detail poet-name="Szymborska"></poet-detail>

      </div>
  </div>
</template>

<script>
import poetDetail from './poetDetail'

export default {
  data () {
    return {
    	szymbo:false,
		poetExpand:'expand-off',
    	poetlist:[{
    		'name':'Szymborska',
    		'poem':'Chwila',
    		'year':'1923-2012',
    		'bkclass':'poet-one'
    	},{
    		'name':'Emily Dickinson',
    		'poem':'ten poem',
    		'year':'1830-1886',
    		'bkclass':'poet-two'
    	},
    	{
    		'name':'Szymborska',
    		'poem':'Chwila',
    		'year':'1923-2012',
    		'bkclass':'poet-three'
    	},{
    		'name':'William Butler Yeats',
    		'poem':'Four year',
    		'year':'1965-1939',
    		'bkclass':'poet-four'
    	}]
    }
  },
  components:{
    poetDetail
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.width-transition {
  transition: all 1s ease;
  transform: translateX(0);
}
.width-enter, .width-leave {
  transform: translateX(-100%);
}
.content{
	padding:0;
	width:94%;
}
.poet{
	width:25%;
	height: 100%;
	display:block;
	overflow:hidden;
	position:absolute;
	top:0;
	transition:all .5s ease-out;
	background-repeat:no-repeat;
	color:#fff;
	&:hover{
		width: 50%;		
		z-index: 9;
		figure {
			animation:running-bk 20s 1;
			&:before{
				height: 40%;
				top:30%;
			}
		}
		
	}

}

.poet figure{
	width:200%;
	height: 100%;
	display:block;
	position:absolute;
	top:0;
	left:0;
	margin:0;
	&:before{
		content: '';
		width: 100%;
		height: 100%;
		display: block;
		position:absolute;
		top: 0;
		left: 0;
		transition:all .5s ease-out;
		background:rgba(0,0,0,0.5);
	}
	

}
@keyframes running-bk {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-40%);
  }
}
.poet-content{
	position:relative;
	top:50%;
	transform:translateY(-50%);
	transition:all .5s ease-out;
	.title{
		font-size: 30px;
		margin:0;
	}
}
.poet-one{
	left:0;
	figure{
		background-image:url('/static/red.jpg');
	}
}
.poet-two{
	left:25%;
	figure{
		background-image:url('/static/purple.jpg');
	}
}
.poet-three{
	left:50%;
	figure{
		background-image:url('/static/blue.jpg');
	}
}
.poet-four{
	left:75%;
	figure{
		background-image:url('/static/ocean.jpg');
	}
	&:hover{
		width: 50%;		
		transform: translateX(-50%);
		z-index: 9;
	}
}
.btn-more{
	margin:0 auto;
	width:100px;
	height: 30px;
	display:block;
	border:1px solid #fff;
	border-radius:20px;
	background:transparent;
	color:#fff;
	cursor:pointer;
	transition:all .3s ease-in;
	&:hover{
		background-color:#67d1d5;
	}
}
.poet-detail{
	height: 100%;
	display:block;
	position:absolute;
	top:0;
	left:0;
	z-index:99;
	background-color:#eee;
	text-align:left;
}
.close{
  width: 40px;
  height: 40px;
  display: block;
  position:absolute;
  top: 20px;
  right: 20px;
  border-radius: 40px;
  border:1px solid #777;
  cursor: pointer;
  span{
    width: 20px;
    height: 1px;
    display: block;
    position:absolute;
    background-color:#777;
    &:first-child{
      top: 20px;
      left: 10px;
      transform: rotateZ(45deg);
    }
    &:nth-child(2){
      top: 20px;
      left: 10px;
      transform: rotateZ(135deg);
    }
  }

}

</style>



