<template>
	<span class="cart" v-on:mouseover="openCart">+Cart</span>
<!--v-bind:class="[cartOpened ? 'isopened':'notopen']"-->
	<div class="shop-cart" transition="fade" v-show="cartopened" v-on:mouseleave="cartopened = false">
		<div class="close" @click="cartopened = false"><span></span><span></span></div>
			<h3 class="font-type">Your Cart</h3>
			<table class="cart-color">
				<thead class="font-type">
					<tr>
						<th width="60%">Name</th>
						<th width="15%">Price</th>
						<th width="20%">Quantity</th>
						<th width="5%"></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="item in shopList" track-by="$index">
						<td>{{item.name}}</td>
						<td>{{item.price}}</td>
						<td>{{item.num}}</td>
						<td><a @click="deletePro(item)" >X</a></td>
					</tr>
				</tbody>
			</table>
			<button class="btn-shop" v-link="{path:'/checkout'}">CheckOut</button>

	</div>

</template>

<script>
var tempStorage = window.sessionStorage;

export default {
  data () {
    return {
    	shopList:[],
    }
  },
  ready:function(){
		this.updateCartList();
		this.$set('shopList',dataList);
  },  
  props: {
  	'cartopened':{
  		default:false,
  	}
},
  methods:{
  	updateCartList:function(){
		dataList = JSON.parse(window.sessionStorage.getItem('cartList'));
		this.$set('shopList',dataList);
  	},
  	openCart:function(){
  		this.cartopened = true;
  		this.updateCartList();
  		console.log(this.shopList);
  	},
  	deletePro:function(item){
  		console.log('delete this pro!');
  		
  		this.shopList.$remove(item);
  		console.log(this.shopList);
  		tempStorage.clear();
  		tempStorage.setItem('cartList',JSON.stringify(this.shopList));
  		tempList.length = 0;
  		tempList = this.shopList;
  		console.log('shopList:'+this.shopList);
  		console.log('sessionStorage:'+window.sessionStorage.cartList);
  		
  	}

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.shop-cart{
	width:400px;
	height: 100%;
	display:block;
	position:absolute;
	top:0;
	right:0;
	z-index:10;
	color:#fff;
	background:rgba(0,0,0,0.8);
	.close{
		span{
			background-color:#fff;
		}
	}
}
.notopen{
	display:none;
}
.isopened{
	display:block;
}
.cart{
  width: 50px;
  height: 35px;
  padding-top: 15px;
  display: block;
  position: fixed;
  right: 6%;
  top:26%;
  border-radius:40px; 
  cursor: pointer;
  text-align:center;
  border: 1px solid #555;
  &:hover{
	  background-color: #fff;
	}
}
.cart-color{
	margin:0 auto;
	width:90%;
	font-size:14px;
	border-collapse: collapse;
	thead{
		tr{
			border-top:1px solid #fff;
			border-bottom:1px solid #fff;
			th{
				padding:10px 0;
			}
		}

	}
	tr{
		border-bottom:1px solid #555;
		td{
			padding:10px 0;
			font-weight: lighter;
			a{
				width: 20px;
				height: 17px;
				padding-top:3px;
				display: block;
				color: #000;
				text-align: center;
				border-radius:50px;
				background:rgba(255,255,255,0.8);
				cursor: pointer;
				&:hover{
					background:#67d1d5;
				}
			}
		}
	}
}
.btn-shop{
	margin-top:20px;
}


</style>


